import React from 'react';
import { setRef } from './setRef';
export function useForkRef(refA, refB) {
    return React.useMemo(() => {
        if (refA === null && refB === null) {
            return null;
        }
        return (value) => {
            setRef(refA, value);
            setRef(refB, value);
        };
    }, [refA, refB]);
}
